	// 台灣統一編號驗證（2023年4月1日後新規則）
	function isValidTaxId(taxId) {
		if (!taxId || taxId.length !== 8 || !/^\d{8}$/.test(taxId)) {
			return false;
		}
		
		const weights = [1, 2, 1, 2, 1, 2, 4, 1];
		let sum = 0;
		
		for (let i = 0; i < 8; i++) {
			let product = parseInt(taxId[i]) * weights[i];
			// 如果乘積是兩位數，拆分相加
			if (product >= 10) {
				product = Math.floor(product / 10) + (product % 10);
			}
			sum += product;
		}
		
		// 能被5整除即為有效統編
		return sum % 5 === 0;
	}

